<template lang='pug'>
    div
        el-card
            .search
                el-form(:inline='true', :model='searchData')
                    el-form-item(label='目的地')
                        el-input(v-model='searchData.destination', placeholder='请输入目的地')
                    el-form-item(label='日期选择')
                        el-date-picker(v-model='searchData.rangeTime', type='daterange', range-separator='至', start-placeholder='入住日期', end-placeholder='离店日期')
                    el-form-item
                        el-input(placeholder='酒店名、地标、商圈')
                    el-form-item
                        el-button(type='primary') 查询
            .condition
                el-form(size='mini')
                    el-form-item.condition__line(label='价格范围')
                        el-checkbox-group(v-model='condi.price')
                            el-checkbox(label='200', name='type') 200以下
                            el-checkbox(label='200-300', name='type') 200-300
                            el-checkbox(label='300-500', name='type') 300-500
                            el-checkbox(label='500', name='type') 500以上
                    el-form-item.condition__line(label='酒店级别')
                        el-checkbox-group(v-model='condi.level')
                            el-checkbox(label='1', name='type') 经济型
                            el-checkbox(label='2', name='type') 二星级/其他
                            el-checkbox(label='3', name='type') 三星级/舒适
                            el-checkbox(label='4', name='type') 四星级/高档
                            el-checkbox(label='5', name='type') 五星级/豪华
                    el-form-item.condition__line(label='连锁品牌')
                        el-checkbox-group(v-model='condi.brand')
                            el-checkbox(label='1', name='type') 维也纳酒店
                            el-checkbox(label='2', name='type') 7天酒店
                            el-checkbox(label='3', name='type') 如家快捷酒店
                            el-checkbox(label='4', name='type') 汉庭酒店
                            el-checkbox(label='5', name='type') 城市便捷酒店
                    el-form-item.condition__line(label='酒店类型')
                        el-checkbox-group(v-model='condi.type')
                            el-checkbox(label='1', name='type') 酒店式公寓
                            el-checkbox(label='2', name='type') 青年旅舍
                            el-checkbox(label='3', name='type') 连锁品牌
                            el-checkbox(label='4', name='type') 家庭旅馆
                            el-checkbox(label='5', name='type') 商务型酒店
                    el-form-item.condition__line(label='入住要求')
                        el-checkbox-group(v-model='condi.command')
                            el-checkbox(label='1', name='type') 免费宽带
                            el-checkbox(label='2', name='type') 游泳池
                            el-checkbox(label='3', name='type') 新开业装修
                            el-checkbox(label='4', name='type') 餐厅
                            el-checkbox(label='5', name='type') 商务中心
            hr
            .main-data
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
                el-card jiudian
</template>

<script>
export default {
    data () {
        return {
            searchData: {
                destination: '',
                rangeTime: ''
            },
            condi: {
                price: [],
                level: [],
                brand: [],
                type: [],
                command: []
            },
            hotel_data: {}
        }
    },
    created () {
        this.$http.post('/jd/searchHotel/', {
            cityCode: '上海',
            begin: '2018-03-04',
            end: '2018-04-01',
            pageCount: 201,
            rowCount: 20,
            saleStatus: 1
        }).then((res) => {
            this.hotel_data = res.data.data
            console.log(res.data)
        })
    }
}
</script>

<style scoped>
    .condition {
        border: 1px solid #ddd;
        padding: 3px 14px;
    }
    .condition__line {
        margin-bottom: 0px;
        font-size: 10px;
    }
    hr {
        margin: 10px 0;
    }
</style>



